// src/pages/Home.js
import { postService, studyService } from "../services/storageService.js";

//      Carousel  import 
import { Carousel } from "react-bootstrap";
import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";

const Home = () => {
  const [newStudies, setNewStudies] = useState([]);
  const [popularStudies, setPopularStudies] = useState([]);
  const [popularPosts, setPopularPosts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    //  
    const fetchData = () => {
      try {
        const newStudiesData = studyService.getNewStudies();
        const popularStudiesData = studyService.getPopularStudies();
        const popularPostsData = postService.getPopularPosts();

        setNewStudies(newStudiesData);
        setPopularStudies(popularStudiesData);
        setPopularPosts(popularPostsData);
      } catch (error) {
        console.error("    :", error);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  //   
  const formatDate = (dateString) => {
    if (!dateString) return "";

    const date = new Date(dateString);

    //   
    if (isNaN(date.getTime())) {
      return dateString;
    }

    return date.toLocaleDateString("ko-KR");
  };

  if (loading) {
    return (
      <div className="text-center">
        <div className="spinner-border" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
      </div>
    );
  }

  return (
    <div>
      {/*    */}
      <Carousel className="mb-5">
        <Carousel.Item>
          <img
            className="d-block w-100"
            src="/images/banner1.jpg"
            alt="Study Hub Banner 1"
            style={{ maxHeight: "500px", objectFit: "cover" }}
          />
          <Carousel.Caption>
            <h3>    </h3>
            <p>      .</p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100"
            src="/images/banner2.jpg"
            alt="Study Hub Banner 2"
            style={{ maxHeight: "500px", objectFit: "cover" }}
          />
          <Carousel.Caption>
            <h3>  </h3>
            <p>       .</p>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>

      <div className="text-center mb-5">
        <h1 className="display-4">    !</h1>
        <p className="lead">      .</p>
      </div>

      {/*     */}
      <section className="mb-5">
        <div className="d-flex justify-content-between align-items-center mb-3">
          <h2>  </h2>
          <Link to="/study-list" className="btn btn-outline-primary btn-sm">
             
          </Link>
        </div>
        <div className="row">
          {newStudies.map((study) => (
            <div className="col-md-4 mb-4" key={study.id}>
              <div className="card h-100">
                <div className="card-body">
                  <h5 className="card-title">{study.title}</h5>
                  <p className="card-text">{study.description}</p>
                  <div className="text-muted small mb-2">
                    <span>
                      : {study.participants || study.memberCount}/
                      {study.maxParticipants || study.maxMembers}
                    </span>
                    <span className="ms-3">
                      : {formatDate(study.createdAt)}
                    </span>
                  </div>
                  <Link
                    to={`/study-apply/${study.id}`}
                    className="btn btn-sm btn-outline-primary"
                  >
                     
                  </Link>
                </div>
              </div>
            </div>
          ))}
        </div>
      </section>

      {/*     */}
      <section className="mb-5">
        <div className="d-flex justify-content-between align-items-center mb-3">
          <h2>  </h2>
          <Link to="/study-list" className="btn btn-outline-primary btn-sm">
             
          </Link>
        </div>
        <div className="row">
          {popularStudies.map((study) => (
            <div className="col-md-4 mb-4" key={study.id}>
              <div className="card h-100">
                <div className="card-body">
                  <div className="d-flex justify-content-between">
                    <h5 className="card-title">{study.title}</h5>
                    <span className="badge bg-warning text-dark">
                       {study.likeCount}
                    </span>
                  </div>
                  <p className="card-text">{study.description}</p>
                  <div className="text-muted small mb-2">
                    <span>
                      : {study.participants || study.memberCount}/
                      {study.maxParticipants || study.maxMembers}
                    </span>
                  </div>
                  <Link
                    to={`/study-apply/${study.id}`}
                    className="btn btn-sm btn-outline-primary"
                  >
                     
                  </Link>
                </div>
              </div>
            </div>
          ))}
        </div>
      </section>

      {/*     */}
      <section>
        <div className="d-flex justify-content-between align-items-center mb-3">
          <h2> </h2>
          <Link
            to="/community/popular"
            className="btn btn-outline-primary btn-sm"
          >
             
          </Link>
        </div>
        <div className="list-group">
          {popularPosts.map((post) => (
            <Link
              to={`/community/post/${post.id}`}
              className="list-group-item list-group-item-action"
              key={post.id}
            >
              <div className="d-flex w-100 justify-content-between align-items-center">
                <h5 className="mb-1">{post.title}</h5>
                <span className="badge bg-warning text-dark">
                   {post.likeCount}
                </span>
              </div>
              <div className="d-flex justify-content-between align-items-center">
                <div>
                  <small className="text-muted">{post.author}</small>
                  <small className="text-muted ms-2">
                     {post.comments?.length || 0}
                  </small>
                </div>
                <small className="text-muted">
                  {formatDate(post.createdAt)}
                </small>
              </div>
            </Link>
          ))}
        </div>
      </section>
    </div>
  );
};

export default Home;
