import { useState, useEffect } from 'react';
import { authService } from '../services/authService.js';
import apiClient from '../services/apiClient.js';

function ProfileSettingsPage() {
    const [showPasswordForm, setShowPasswordForm] = useState(false);
    const [password, setPassword] = useState('');
    const [newPassword, setNewPassword] = useState('');

    const [showProfileForm, setShowProfileForm] = useState(false);

    const [name, setName] = useState('');
    const [university, setUniversity] = useState('');
    const [certificates, setCertificates] = useState('');
    const [age, setAge] = useState('24');
    const [gender, setGender] = useState('');
    const [location, setLocation] = useState('');
    const [phone, setPhone] = useState('010-1234-5678');

    useEffect(() => {
        const fetchProfile = async () => {
            try {
                const profile = await authService.getProfile(); // getProfile  
                setName(profile.name || '');
                setUniversity(profile.university || '');
                setCertificates(profile.certificate || '');
                setAge(profile.age || '');
                setGender(profile.gender || '');
                setLocation(profile.region || '');
                setPhone(profile.phone || '');
            } catch (err) {
                console.error("   .", err);
                alert("    .");
            }
        };

        fetchProfile();
    }, []);

    const togglePasswordForm = () => {
        setShowPasswordForm(!showPasswordForm);
    };

    const toggleProfileForm = () => {
        setShowProfileForm(!showProfileForm);
    };

    const handleDelete = async () => {
        const confirmDelete = window.confirm(" ?     .");
        if (!confirmDelete) return;

        try {
            await authService.deleteAccount();
            localStorage.removeItem("token"); //  
            authService.logout();
            alert(" .");
            window.location.href = "/signin";
        } catch (err) {
            console.error(err);
            alert("  .");
        }
    };

    const handlePasswordChange = async (e) => {
        e.preventDefault();

        try {
            await authService.changePassword(password, newPassword);
            alert("  .");
            setPassword("");
            setNewPassword("");
            setShowPasswordForm(false);
        } catch (error) {
            alert(error.message);
        }
    };

    const handleProfileChange = async (e) => {
        e.preventDefault();

        const payload = {
  nickname: name,
  university,
  certificate: certificates,
  age: Number(age),
  region: location,    // location region  
  phone,
  gender,
};



  console.log("  payload:", payload);

        try {
            await apiClient.patch('/auth/profile-update', payload);
            alert("   .");
            setShowProfileForm(false);
        } catch (error) {
            console.error(error);
            alert("  .");
        }
    };



    return (
        <div style={styles.container}>
            <h1 style={styles.title}>  </h1>

            {/*     */}
            <div style={styles.profileBox}>
                <h3 style={styles.profileTitle}>  </h3>
                <p><strong>:</strong> {name}</p>
                <p><strong>:</strong> {university}</p>
                <p><strong>:</strong> {certificates}</p>
                <p><strong>:</strong> {age}</p>
                <p><strong>:</strong> {gender}</p>
                <p><strong>:</strong> {location}</p>
                <p><strong>:</strong> {phone}</p>
            </div>

            {/*     */}
            <button onClick={toggleProfileForm} style={styles.button}>
                   
            </button>

            {showProfileForm && (
                <form onSubmit={handleProfileChange} style={styles.form}>
                    <label style={styles.label}></label>
                    <input
                        type="text"
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                        style={styles.input}
                        placeholder=" "
                    />

                    <label style={styles.label}></label>
                    <input
                        type="text"
                        value={university}
                        onChange={(e) => setUniversity(e.target.value)}
                        style={styles.input}
                        placeholder=" "
                    />

                    <label style={styles.label}> </label>
                    <input
                        type="text"
                        value={certificates}
                        onChange={(e) => setCertificates(e.target.value)}
                        style={styles.input}
                        placeholder=" "
                    />

                    <label style={styles.label}></label>
                    <input
                        type="number"
                        value={age}
                        onChange={(e) => setAge(e.target.value)}
                        style={styles.input}
                        placeholder=" "
                        min="0"
                    />

                    <label style={styles.label}></label>
                    <select
                        value={gender}
                        onChange={(e) => setGender(e.target.value)}
                        style={{ ...styles.input, padding: '12px' }}
                    >
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                    </select>

                    <label style={styles.label}></label>
                    <input
                        type="text"
                        value={location}
                        onChange={(e) => setLocation(e.target.value)}
                        style={styles.input}
                        placeholder=" "
                    />

                    <label style={styles.label}></label>
                    <input
                        type="tel"
                        value={phone}
                        onChange={(e) => setPhone(e.target.value)}
                        style={styles.input}
                        placeholder=" "
                    />

                    <button type="submit" style={{ ...styles.button, backgroundColor: '#5dade2' }}>
                         
                    </button>
                </form>
            )}

            {/*    */}
            <button onClick={togglePasswordForm} style={{ ...styles.button, backgroundColor: '#3498db' }}>
                  
            </button>

            {showPasswordForm && (
                <form onSubmit={handlePasswordChange} style={styles.form}>
                    <label style={styles.label}> </label>
                    <input
                        type="password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        style={styles.input}
                        placeholder="  "
                    />

                    <label style={styles.label}> </label>
                    <input
                        type="password"
                        value={newPassword}
                        onChange={(e) => setNewPassword(e.target.value)}
                        style={styles.input}
                        placeholder="  "
                    />

                    <button type="submit" style={{ ...styles.button, backgroundColor: '#2980b9' }}>
                         
                    </button>
                </form>
            )}

            <button onClick={handleDelete} style={styles.deleteButton}>
                  
            </button>
        </div>
    );
}

const styles = {
    container: {
        maxWidth: '480px',
        margin: '60px auto',
        padding: '35px',
        border: '1px solid #ddd',
        borderRadius: '14px',
        boxShadow: '0 6px 18px rgba(0,0,0,0.08)',
        textAlign: 'center',
        backgroundColor: '#fefefe',
    },
    title: {
        marginBottom: '28px',
        color: '#34495e',
        fontSize: '26px',
    },
    profileBox: {
        textAlign: 'left',
        padding: '20px',
        border: '1px solid #ccc',
        borderRadius: '10px',
        marginBottom: '25px',
        backgroundColor: '#f9f9f9',
    },
    profileTitle: {
        marginBottom: '15px',
        fontSize: '18px',
        color: '#2c3e50',
    },
    form: {
        marginTop: '20px',
        marginBottom: '30px',
        textAlign: 'left',
    },
    label: {
        display: 'block',
        margin: '10px 0 5px',
        color: '#2c3e50',
        fontSize: '15px',
    },
    input: {
        width: '100%',
        padding: '12px',
        marginBottom: '18px',
        borderRadius: '8px',
        border: '1px solid #ccc',
        fontSize: '14px',
        boxSizing: 'border-box',
    },
    button: {
        width: '100%',
        padding: '13px',
        backgroundColor: '#76d7c4',
        color: 'white',
        border: 'none',
        borderRadius: '8px',
        cursor: 'pointer',
        fontSize: '16px',
        marginBottom: '12px',
        transition: 'background-color 0.3s',
    },
    deleteButton: {
        width: '100%',
        padding: '13px',
        backgroundColor: '#e74c3c',
        color: 'white',
        border: 'none',
        borderRadius: '8px',
        cursor: 'pointer',
        fontSize: '16px',
        transition: 'background-color 0.3s',
    },
};

export default ProfileSettingsPage;
