import { studyService } from "../services/studyService.js";
import { useAuth } from "../contexts/AuthContext.js";
import { useParams, useNavigate, useLocation } from "react-router-dom";
import { useState, useEffect } from "react";

const StudyApply = () => {
  const { id } = useParams();
  const { isAuthenticated, currentUser } = useAuth();
  const navigate = useNavigate();
  const location = useLocation();
  const canApply = location.state?.canApply ?? false;

  const [study, setStudy] = useState(null);
  const [loading, setLoading] = useState(true);
  const [applicationData, setApplicationData] = useState({
    name: "",
    availableTime: "",
    reason: "",
  });

  useEffect(() => {
  const fetchStudy = async () => {
    try {
      const foundStudy = await studyService.getStudyById(id);
      setStudy(foundStudy);

      if (currentUser) {
        setApplicationData((prev) => ({
          ...prev,
          name: currentUser.nickname || "",
        }));
      }
    } catch (error) {
      console.error("   :", error);
    } finally {
      setLoading(false);
    }
  };

  fetchStudy();
}, [id, currentUser]);


  const handleChange = (e) => {
    const { name, value } = e.target;
    setApplicationData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = async (e) => {
  e.preventDefault();

  try {
    await studyService.applyToStudy(id, {
      userId: currentUser.id,
      name: applicationData.name, 
      available_time: applicationData.availableTime,
      reason: applicationData.reason,
    });

    alert("  !");
    navigate("/study-list");
  } catch (error) {
    console.error("  :", error);
    alert(" .  .");
  }
};


  if (loading) {
    return (
      <div className="container py-4 text-center">
        <div className="spinner-border" role="status">
          <span className="visually-hidden"> ...</span>
        </div>
      </div>
    );
  }

  if (!study) {
    return (
      <div className="container py-4 text-center">
        <div className="alert alert-danger">   .</div>
        <button
          onClick={() => navigate("/study-list")}
          className="btn btn-primary"
        >
          
        </button>
      </div>
    );
  }

  return (
    <div className="container py-4">
      <h2 className="text-center mb-4">   {canApply && " & "}</h2>

      <div className="mb-4 p-3 bg-light rounded">
        <h3>{study.title}</h3>
        <p>: {study.subject}</p>
        <p>: {study.category}</p>
        <p>
          : {study.current_members}/{study.max_members}
        </p>
        {study.description && <p>{study.description}</p>}
      </div>

      {/*   */}
      {canApply ? (
        isAuthenticated ? (
          <form onSubmit={handleSubmit}>
            <div className="mb-3">
              <label className="form-label"></label>
              <input
                type="text"
                className="form-control"
                name="name"
                value={applicationData.name}
                onChange={handleChange}
                placeholder=" "
              />
            </div>
            <div className="mb-3">
              <label className="form-label">  </label>
              <input
                type="text"
                className="form-control"
                name="availableTime"
                value={applicationData.availableTime}
                onChange={handleChange}
                placeholder="  "
                required
              />
            </div>

            <div className="mb-3">
              <label className="form-label"> </label>
              <textarea
                className="form-control"
                name="reason"
                value={applicationData.reason}
                onChange={handleChange}
                placeholder="  "
                rows="3"
                required
              />
            </div>

            <div className="d-grid">
              <button type="submit" className="btn btn-success">
                
              </button>
            </div>
          </form>
        ) : (
          <div className="alert alert-warning text-center">
                 .
          </div>
        )
      ) : null}
    </div>
  );
};

export default StudyApply;
