import { postService } from "../services/postsService.js";
import { useAuth } from "../contexts/AuthContext.js";

import { Link, useParams, useNavigate } from "react-router-dom";
import React, { useState, useEffect } from "react";

const CreatePost = () => {
  const { category } = useParams();
  const navigate = useNavigate();
  const { currentUser, isAuthenticated, isAdmin } = useAuth();

  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [loading, setLoading] = useState(false);
  const [categoryError, setCategoryError] = useState("");

  const getCategoryTitle = () => {
    switch (category) {
      case "notice":
        return "";
      case "freetalk":
        return "";
      case "qna":
        return "Q&A";
      default:
        return "";
    }
  };

  useEffect(() => {
    if (category === "notice" && !isAdmin) {
      setCategoryError("    .");
    } else {
      setCategoryError("");
    }

    if (!isAuthenticated) {
      navigate("/signin", { state: { from: `/community/create/${category}` } });
    }
  }, [category, isAdmin, isAuthenticated, navigate]);

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (category === "notice" && !isAdmin) {
      alert("    .");
      return;
    }

    if (!title.trim() || !content.trim()) {
      alert("   .");
      return;
    }

    setLoading(true);

    try {
      const postData = {
        title,
        content,
        author_id: currentUser.id,
        category,
        isQuestion: category === "qna",
        hasAnswer: false,
      };

      await postService.createPost(postData);
      navigate(`/community/${category}`);
    } catch (error) {
      console.error("  :", error);
      alert(error.message || "    .");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="row">
      <div className="col-12">
        <h2>{getCategoryTitle()}  </h2>

        {categoryError && (
          <div className="alert alert-danger" role="alert">
            {categoryError}
          </div>
        )}

        <form onSubmit={handleSubmit}>
          <div className="mb-3">
            <label htmlFor="title" className="form-label"></label>
            <input
              type="text"
              className="form-control"
              id="title"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              required
              disabled={Boolean(categoryError)}  // Boolean 
            />
          </div>

          <div className="mb-3">
            <label htmlFor="content" className="form-label"></label>
            <textarea
              className="form-control"
              id="content"
              rows="10"
              value={content}
              onChange={(e) => setContent(e.target.value)}
              required
              disabled={Boolean(categoryError)}  // Boolean 
            />
          </div>

          <div className="mb-3">
            <button
              type="submit"
              className="btn btn-primary"
              disabled={loading || Boolean(categoryError)}
            >
              {loading ? (
                <>
                  <span
                    className="spinner-border spinner-border-sm"
                    role="status"
                    aria-hidden="true"
                  />
                  <span className="ms-2"> ...</span>
                </>
              ) : (
                ""
              )}
            </button>
            <Link to={`/community/${category}`} className="btn btn-secondary ms-2"></Link>
          </div>
        </form>
      </div>
    </div>
  );
};

export default CreatePost;
