import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import apiClient from "../services/apiClient.js"; // API  

function MyStudies() {
  const [studies, setStudies] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchStudies = async () => {
      try {
        const res = await apiClient.get("/studies/my"); //   
        console.log("  :", res.data);
        setStudies(res.data);
      } catch (err) {
        alert("   .");
      } finally {
        setLoading(false);
      }
    };

    fetchStudies();
  }, []);

  if (loading) return <p className="text-center"> ...</p>;

  return (
    <div className="container py-4">
      <h2 className="text-center mb-4">   </h2>

      <div className="row">
        {studies.map((study) => (
          <div key={study.id} className="col-md-6 mb-4">
            <div className="card">
              <div className="card-body">
                <h3 className="card-title">{study.title}</h3>
                <p className="card-text">
                  : {study.current_members}/{study.max_members}
                </p>
                <p className="card-text">
                  : {new Date(study.created_at).toLocaleDateString()}
                </p>
                <Link
                  to={`/study-apply/${study.id}`}
                  state={{ canApply: false }}
                  className="btn btn-primary"
                >
                   
                </Link>
              </div>
            </div>
          </div>
        ))}
      </div>

      {studies.length === 0 && (
        <div className="alert alert-info text-center">
             .  !
        </div>
      )}

      <div className="text-center mt-4">
        <Link to="/study-list" className="btn btn-outline-primary">
            
        </Link>
      </div>
    </div>
  );
}

export default MyStudies;
