// src/pages/PostDetail.js
import { postService } from "../services/postsService.js";
import { useAuth } from "../contexts/AuthContext.js";

import { Link, useParams, useNavigate } from "react-router-dom";
import React, { useState, useEffect } from "react";

const PostDetail = ({ type }) => {
  const { category: categoryParam, id } = useParams();
  const category = categoryParam || type;
  const { currentUser, isAuthenticated, isAdmin } = useAuth(); // isAdmin 
  const navigate = useNavigate();

  const [post, setPost] = useState(null);
  const [loading, setLoading] = useState(true);
  const [commentContent, setCommentContent] = useState("");
  const [likeStatus, setLikeStatus] = useState(false);

  // /   
  const [isEditingPost, setIsEditingPost] = useState(false);
  const [editTitle, setEditTitle] = useState("");
  const [editContent, setEditContent] = useState("");
  const [editingCommentId, setEditingCommentId] = useState(null);
  const [editCommentContent, setEditCommentContent] = useState("");


  //   
  const fetchPost = async () => {
    try {
      // id     ( await )
      const fetchedPost = await postService.getPostById(category, id);
      console.log(fetchedPost.comments);

      if (fetchedPost) {
        //   API  (await )
        await postService.increaseViews(category, id);



        //  
        setPost(fetchedPost);

        //   
        if (currentUser && fetchedPost.likedBy) {
          setLikeStatus(fetchedPost.likedBy.includes(currentUser.id));
        }
      }
    } catch (error) {
      console.error("    :", error);
    } finally {
      setLoading(false);
    }
  };


  useEffect(() => {
    setLoading(true);
    fetchPost();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [id, currentUser]);


  // 
  const handleCommentSubmit = async (e) => {
    e.preventDefault();

    if (!isAuthenticated) {
      if (window.confirm("   .   ?")) {
        navigate("/signin", { state: { from: `/community/post/${id}` } });
      }
      return;
    }

    if (!commentContent.trim()) return;

    const newComment = {
      content: commentContent,
      author: currentUser.name,
      authorId: currentUser.id,
      isAnswer: post.isQuestion && !post.hasAnswer,
    };

    try {
      if (newComment.isAnswer) {
        await postService.addAnswer(parseInt(id), newComment);
      } else {
        await postService.addComment(category, parseInt(id), newComment);
      }

      await fetchPost(); //      
      setCommentContent(""); //  
    } catch (error) {
      console.error("   :", error);
      alert("  .");
    }
  };



  // 
  const handleLike = async () => {
    if (!isAuthenticated) {
      const confirmLogin = window.confirm(
        "   .   ?"
      );

      if (confirmLogin) {
        navigate("/signin", { state: { from: `/community/post/${id}` } });
      }
      return;
    }

    try {
      const updatedPost = await postService.likePost(category, id);

      if (updatedPost) {
        //       
        setPost((prevPost) => ({
          ...prevPost,
          likes: updatedPost.likes,
          likedBy: updatedPost.likedBy,
          //    
        }));

        setLikeStatus(updatedPost.likedBy?.includes(currentUser.id));
      }
    } catch (error) {
      console.error("    :", error);
    }
  };




  //    
  const handleEditPost = () => {
    setIsEditingPost(true);
    setEditTitle(post.title);
    setEditContent(post.content);
  };

  //   
  const handleCancelEditPost = () => {
    setIsEditingPost(false);
  };

  //   
  const handleSavePost = async () => {
    if (!editTitle.trim() || !editContent.trim()) {
      alert("   .");
      return;
    }

    try {
      const updatedPost = await postService.updatePost(
        category,  //  category 
        id,   //  postId
        {
          title: editTitle,
          content: editContent,
        }
      );

      setPost(updatedPost.data);  // axios  data 
      setIsEditingPost(false);
      alert(" .");
    } catch (error) {
      console.error("    :", error);
      alert(error.response?.data?.error || "  .");
    }
  };


  //  
  const handleDeletePost = async () => {
    if (!window.confirm("   ?")) {
      return;
    }

    try {
      await postService.deletePost(category, parseInt(id));
      alert(" .");

      //     
      navigate(`/community/${category}`);
    } catch (error) {
      console.error("    :", error);
      alert(error.message || "  .");
    }
  };


  //    
  const handleEditComment = (comment) => {
    setEditingCommentId(comment.id);
    setEditCommentContent(comment.content);
  };

  //   
  const handleCancelEditComment = () => {
    setEditingCommentId(null);
    setEditCommentContent("");
  };

  //   
  const handleSaveComment = async (commentId) => {
    if (!editCommentContent.trim()) {
      alert("  .");
      return;
    }

    try {
      await postService.updateComment(category, id, commentId, editCommentContent);
      await fetchPost();  //      

      setEditingCommentId(null);
      setEditCommentContent("");
      alert(" .");
    } catch (error) {
      console.error("   :", error);
      alert("  .");
    }
  };



  //  
  const handleDeleteComment = async (commentId) => {
    if (!window.confirm("   ?")) return;

    try {
      await postService.deleteComment(category, parseInt(id), commentId);
      await fetchPost();  //       
      alert(" .");
    } catch (error) {
      console.error("   :", error);
      alert("  .");
    }
  };



  //      
  const isPostAuthor = () => {
    if (!isAuthenticated || !currentUser || !post) return false;

    //        
    return post.author === currentUser.name || isAdmin;
  };

  //      
  const isCommentAuthor = (comment) => {
    if (!isAuthenticated || !currentUser) return false;

    //        
    return comment.author === currentUser.name || isAdmin;
  };

  if (loading) {
    return (
      <div className="text-center">
        <div className="spinner-border" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
      </div>
    );
  }

  if (!post) {
    return (
      <div className="alert alert-danger" role="alert">
           .
      </div>
    );
  }

  return (
    <div className="row">
      <div className="col-12">
        <div className="mb-3 d-flex justify-content-between align-items-center">
          <Link
            to={`/community/${category}`}
            className="btn btn-outline-secondary"
          >
            
          </Link>

          {/*      /   */}
          {isPostAuthor() && (
            <div>
              <button
                className="btn btn-outline-primary me-2"
                onClick={handleEditPost}
              >
                
              </button>
              <button
                className="btn btn-outline-danger"
                onClick={handleDeletePost}
              >
                
              </button>
            </div>
          )}
        </div>

        <div className="card mb-4">
          <div className="card-header d-flex justify-content-between">
            {isEditingPost ? (
              <input
                type="text"
                className="form-control"
                value={editTitle}
                onChange={(e) => setEditTitle(e.target.value)}
              />
            ) : (
              <h3>{post.title}</h3>
            )}
            {!isEditingPost && (
              <div>
                <span className="text-muted">
                  {post.author} | {post.createdAt}
                  {post.updatedAt && ` (: ${post.updatedAt})`}
                </span>
                <span className="ms-2"> {post.viewCount}</span>
              </div>
            )}
          </div>
          <div className="card-body">
            {isEditingPost ? (
              <div>
                <textarea
                  className="form-control mb-3"
                  rows="10"
                  value={editContent}
                  onChange={(e) => setEditContent(e.target.value)}
                ></textarea>
                <div className="d-flex justify-content-end">
                  <button
                    className="btn btn-secondary me-2"
                    onClick={handleCancelEditPost}
                  >
                    
                  </button>
                  <button className="btn btn-primary" onClick={handleSavePost}>
                    
                  </button>
                </div>
              </div>
            ) : (
              <div className="post-content mb-4 white-space-pre-line">
                {(post.content || "").split("\n").map((line, i) => (
                  <React.Fragment key={i}>
                    {line}
                    <br />
                  </React.Fragment>
                ))}

              </div>
            )}

            {!isEditingPost && (
              <div className="d-flex justify-content-end">
                <button
                  className={`btn ${likeStatus ? "btn-danger" : "btn-outline-danger"
                    }`}
                  onClick={handleLike}
                >
                    {post.likes}
                </button>
              </div>
            )}
          </div>
        </div>

        {!isEditingPost && (
          <div className="card mb-4">
            <div className="card-header">
              <h4>
                {post.isQuestion && !post.hasAnswer ? " " : ""}{" "}
                {post.comments?.length > 0 ? `(${post.comments.length})` : ""}
              </h4>
            </div>
            <div className="card-body">
              {post.comments && post.comments.length > 0 ? (
                post.comments.map((comment) => (
                  <div
                    key={comment.id}
                    className={`comment mb-3 ${comment.isAnswer ? "bg-light p-3" : ""
                      }`}
                  >
                    <div className="d-flex justify-content-between">
                      <div>
                        <strong>{comment.author}</strong>
                        {comment.isAnswer && (
                          <span className="badge bg-success ms-2"></span>
                        )}
                      </div>
                      <div>
                        <span className="text-muted">
                          {comment.createdAt}
                          {comment.isEdited && " ()"}
                        </span>

                        {/*      /   */}
                        {isCommentAuthor(comment) &&
                          editingCommentId !== comment.id && (
                            <>
                              <button
                                className="btn btn-sm btn-outline-secondary ms-2"
                                onClick={() => handleEditComment(comment)}
                              >
                                
                              </button>
                              <button
                                className="btn btn-sm btn-outline-danger ms-1"
                                onClick={() => handleDeleteComment(comment.id)}
                              >
                                
                              </button>
                            </>
                          )}
                      </div>
                    </div>

                    {editingCommentId === comment.id ? (
                      <div className="mt-2">
                        <textarea
                          className="form-control mb-2"
                          rows="3"
                          value={editCommentContent}
                          onChange={(e) =>
                            setEditCommentContent(e.target.value)
                          }
                        ></textarea>
                        <div className="d-flex justify-content-end">
                          <button
                            className="btn btn-sm btn-secondary me-2"
                            onClick={handleCancelEditComment}
                          >
                            
                          </button>
                          <button
                            className="btn btn-sm btn-primary"
                            onClick={() => handleSaveComment(comment.id)}
                          >
                            
                          </button>
                        </div>
                      </div>
                    ) : (
                      <div className="mt-2">{comment.content}</div>
                    )}
                  </div>
                ))
              ) : (
                <div className="text-center text-muted mb-3">
                  {post.isQuestion
                    ? "  ."
                    : "  ."}
                </div>
              )}

              {!editingCommentId && (
                <form onSubmit={handleCommentSubmit}>
                  <div className="mb-3">
                    <textarea
                      className="form-control"
                      rows="3"
                      placeholder={
                        isAuthenticated
                          ? post.isQuestion && !post.hasAnswer
                            ? " "
                            : " "
                          : " "
                      }
                      value={commentContent}
                      onChange={(e) => setCommentContent(e.target.value)}
                      disabled={!isAuthenticated}
                      required
                    />
                  </div>
                  <div className="text-end">
                    {isAuthenticated ? (
                      <button type="submit" className="btn btn-primary">
                        {post.isQuestion && !post.hasAnswer
                          ? " "
                          : " "}
                      </button>
                    ) : (
                      <Link
                        to="/signin"
                        className="btn btn-primary"
                        state={{ from: `/community/post/${id}` }}
                      >
                          
                      </Link>
                    )}
                  </div>
                </form>
              )}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default PostDetail;
