import { useAuth } from "../contexts/AuthContext.js";
import { Link, NavLink, useLocation, useNavigate } from "react-router-dom";
import React, { useEffect, useState } from "react";
import apiClient from "../services/apiClient.js";

const Header = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const { currentUser, logout, isAuthenticated, isAdmin } = useAuth();
  const [searchTerm, setSearchTerm] = useState("");

  const [hostedStudyId, setHostedStudyId] = useState(null);

  useEffect(() => {
    const fetchHostedStudyId = async () => {
      try {
        const res = await apiClient.get("/studies/hosted");
        const data = res.data;

        if (data.length > 0) {
          setHostedStudyId(data[0].id);
        }
      } catch (err) {
        console.error("  ID  :", err);
      }
    };

    if (isAuthenticated) {
      fetchHostedStudyId();
    }
  }, [isAuthenticated]);


  const handleSearch = (e) => {
    e.preventDefault();
    if (searchTerm.trim()) {
      navigate(`/search?q=${encodeURIComponent(searchTerm)}`);
      setSearchTerm("");
    }
  };

  const handleLogout = () => {
    logout();
    navigate("/");
  };


  return (
    <header>
      <div className="container">
        {/*   */}
        <div className="text-center py-3">
          <h1 className="site-title">
            <Link to="/" className="text-decoration-none text-dark">
              Study Hub
            </Link>
          </h1>

          {/*  */}
          <div className="row justify-content-center my-3">
            <div className="col-md-6">
              <form className="d-flex" onSubmit={handleSearch}>
                <input
                  className="form-control me-2"
                  type="search"
                  placeholder=" "
                  aria-label="Search"
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  style={{ maxWidth: "80%", flexGrow: 1 }}
                />
                <button type="submit" className="btn btn-outline-primary">
                  
                </button>
              </form>
            </div>
          </div>
        </div>

        {/*   */}
        <div className="main-menu py-2 border-top border-bottom">
          <ul className="nav justify-content-center">
            <li className="nav-item px-4">
              <NavLink
                className={({ isActive }) =>
                  isActive ? "nav-link fw-bold active" : "nav-link"
                }
                to="/study-list"
              >
                List
              </NavLink>
            </li>

            {/* MY STUDY  -    */}
            {isAuthenticated && (
              <li className="nav-item px-4">
                <NavLink
                  className={({ isActive }) =>
                    isActive ? "nav-link fw-bold active" : "nav-link"
                  }
                  to="/my-studies"
                >
                  My Study
                </NavLink>
              </li>
            )}

            <li className="nav-item px-4">
              <NavLink
                className={({ isActive }) =>
                  location.pathname.includes("/community")
                    ? "nav-link fw-bold active"
                    : "nav-link"
                }
                to="/community/notice"
              >
                Community
              </NavLink>
            </li>
          </ul>
        </div>

        {/* /     */}
        <div className="position-absolute top-0 end-0 p-3">
          {isAuthenticated ? (
            <div className="dropdown">
              <button
                className="btn btn-sm btn-outline-secondary dropdown-toggle"
                type="button"
                id="userMenu"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                {currentUser?.nickname}
                {isAdmin && (
                  <span className="badge bg-danger ms-1"></span>
                )}
              </button>
              <ul
                className="dropdown-menu dropdown-menu-end"
                aria-labelledby="userMenu"
              >
                <li>
                  <Link className="dropdown-item" to="/profile-settings">
                    
                  </Link>
                </li>
                <li>
                  <Link className="dropdown-item" to="/my-community">
                     
                  </Link>
                </li>
                <li>
                  <Link
                    className="dropdown-item"
                    to={hostedStudyId ? `/studies/${hostedStudyId}/manage` : "#"}>
                      
                  </Link>
                </li>
                {isAdmin && (
                  <>
                    <li>
                      <hr className="dropdown-divider" />
                    </li>
                    <li>
                      <Link
                        className="dropdown-item"
                        to="/community/create/notice"
                      >
                         
                      </Link>
                    </li>
                  </>
                )}
                <li>
                  <hr className="dropdown-divider" />
                </li>
                <li>
                  <button className="dropdown-item" onClick={handleLogout}>
                    
                  </button>
                </li>
              </ul>
            </div>
          ) : (
            <>
              <Link
                to="/signin"
                className="btn btn-sm btn-outline-secondary me-2"
              >
                
              </Link>
              <Link to="/signup" className="btn btn-sm btn-outline-primary">
                
              </Link>
            </>
          )}
        </div>
      </div>
    </header>
  );
};

export default Header;
