// src/pages/PostList.js
import { postService } from "../services/postsService.js";
import { useAuth } from "../contexts/AuthContext.js";
import CommunityNav from "../components/CommunityNav.js";

import { Link, useParams } from "react-router-dom";
import React, { useState, useEffect } from "react";

const PostList = () => {
  const { category } = useParams();
  const { isAuthenticated } = useAuth();

  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [filteredPosts, setFilteredPosts] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const postsPerPage = 5; //  5 

  // src/pages/PostList.js useEffect  
  useEffect(() => {
  const fetchPosts = async () => {
    setLoading(true);
    try {
      //    
      const validCategories = ["notice", "freetalk", "qna"];

      // category validCategories    ,  "qna"  
      const serverCategory = validCategories.includes(category) ? category : "qna";

      let data;
      if (category === "popular") {
        data = await postService.getPopularPosts();
      } else {
        data = await postService.getPosts(serverCategory);
      }

      //  
      const sortedByIdDesc = [...data].sort((a, b) => b.views - a.views);

      setPosts(sortedByIdDesc);
      setFilteredPosts(sortedByIdDesc);
      setCurrentPage(1);
    } catch (error) {
      console.error("    :", error);
    } finally {
      setLoading(false);
    }
  };

  fetchPosts();
}, [category]);


  //  
  const handleSearch = (e) => {
    e.preventDefault();

    if (!searchTerm.trim()) {
      setFilteredPosts(posts);
      return;
    }

    const searchTermLower = searchTerm.toLowerCase();
    const filtered = posts.filter(
      (post) =>
        post.title.toLowerCase().includes(searchTermLower) ||
        post.author.toLowerCase().includes(searchTermLower)
    );

    setFilteredPosts(filtered);
    setCurrentPage(1); //     
  };

  //  
  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;
  const currentPosts = filteredPosts.slice(indexOfFirstPost, indexOfLastPost);
  const totalPages = Math.ceil(filteredPosts.length / postsPerPage);

  //   
  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  return (
    <div className="row">
      <div className="col-12">
        <CommunityNav category={category} />

        <div className="mb-3 d-flex justify-content-center">
          <form
            className="d-flex"
            onSubmit={handleSearch}
            style={{ width: "70%" }}
          >
            <input
              className="form-control me-2"
              type="search"
              placeholder=" "
              aria-label="Search"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              style={{ flexGrow: 1 }}
            />
            <button
              className="btn btn-outline-primary"
              type="submit"
              style={{ minWidth: "100px" }}
            >
              
            </button>
          </form>
        </div>

        {loading ? (
          <div className="text-center">
            <div className="spinner-border" role="status">
              <span className="visually-hidden">Loading...</span>
            </div>
          </div>
        ) : (
          <>
            <table className="table">
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {currentPosts.length > 0 ? (
  currentPosts.map((post, index) => (
    <tr key={`${post.category}-${post.id}`}>
      <td>{indexOfFirstPost + index + 1}</td>
      <td>
        <Link to={`/community/${post.category}/post/${post.id}`}>
          {post.title}
          {post.comments && post.comments.length > 0 && (
            <span className="text-muted ms-1">
              [{post.comments.length}]
            </span>
          )}
          {post.isQuestion && post.hasAnswer && (
            <span className="badge bg-success ms-1">
              
            </span>
          )}
        </Link>
      </td>
      <td>{post.author}</td>
      <td>{new Date(post.created_at).toLocaleDateString()}</td>
      <td>{post.views}</td>
    </tr>
  ))
) : (
  <tr>
    <td colSpan="5" className="text-center"> .</td>
  </tr>
)}

              </tbody>
            </table>

            <div className="d-flex justify-content-between">
              {/*  */}
              {totalPages > 1 && (
                <nav aria-label="Page navigation">
                  <ul className="pagination">
                    {/*   */}
                    <li
                      className={`page-item ${currentPage === 1 ? "disabled" : ""
                        }`}
                    >
                      <button
                        className="page-link"
                        onClick={() => paginate(currentPage - 1)}
                        disabled={currentPage === 1}
                      >
                        
                      </button>
                    </li>

                    {/*   */}
                    {Array.from({ length: totalPages }, (_, i) => (
                      <li
                        key={i + 1}
                        className={`page-item ${currentPage === i + 1 ? "active" : ""
                          }`}
                      >
                        <button
                          className="page-link"
                          onClick={() => paginate(i + 1)}
                        >
                          {i + 1}
                        </button>
                      </li>
                    ))}

                    {/*   */}
                    <li
                      className={`page-item ${currentPage === totalPages ? "disabled" : ""
                        }`}
                    >
                      <button
                        className="page-link"
                        onClick={() => paginate(currentPage + 1)}
                        disabled={currentPage === totalPages}
                      >
                        
                      </button>
                    </li>
                  </ul>
                </nav>
              )}

              {/*       */}
              {totalPages <= 1 && (
                <nav aria-label="Page navigation">
                  <ul className="pagination">
                    <li className="page-item disabled">
                      <button className="page-link" disabled>
                        
                      </button>
                    </li>
                    <li className="page-item active">
                      <button className="page-link">1</button>
                    </li>
                    <li className="page-item disabled">
                      <button className="page-link" disabled>
                        
                      </button>
                    </li>
                  </ul>
                </nav>
              )}

              {isAuthenticated ? (
                <Link
                  to={`/community/create/${category}`}
                  className="btn btn-primary" // btn-sm  
                  style={{
                    fontSize: "0.875rem", //   
                    padding: "0.375rem 0.75rem", //   
                    lineHeight: "1.5",
                    borderRadius: "0.25rem",
                    height: "38px", //  
                    display: "inline-flex", //   
                    alignItems: "center", //   
                    justifyContent: "center", //  
                  }}
                >
                  
                </Link>
              ) : (
                <Link
                  to="/signin"
                  state={{ from: `/community/create/${category}` }}
                  className="btn btn-primary" // btn-sm  
                  style={{
                    fontSize: "0.875rem", //   
                    padding: "0.375rem 0.75rem", //   
                    lineHeight: "1.5",
                    borderRadius: "0.25rem",
                    height: "38px", //  
                    display: "inline-flex", //  
                    alignItems: "center", //  
                    justifyContent: "center", //  
                  }}
                >
                    
                </Link>
              )}
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default PostList;
