import apiClient from "./apiClient.js";

export const searchService = {
  async search(query) {
    try {
      const response = await apiClient.get("/search", {
        params: { q: query },
      });
      return response.data; // { posts: [...], studies: [...] }  
    } catch (error) {
      console.error("  :", error);
      throw error;
    }
  },
};
